import React from 'react';

class DoOpposite extends React.Component {
    render() {
        return (
            <view className="form-check">
                <ol className="form-check-label">
                    <li>Open opposite direction @ {this.props.opentime} or {this.props.pricetoopen}</li>
                    <li>Close @ {this.props.closetime} or {this.props.pricetoclose}</li>
                </ol>
            </view>
        );
    }
}

class DoSameDirection extends React.Component {
    render() {
        return (
            <view className="form-check">
                <ol className="form-check-label">
                    <li>Open same direction @10:30 or @50% retrace</li>
                    <li>Close @same day or on day of @3rd highest high or lowest low or @100% profit</li>
                </ol>
            </view>
        );
    }
}

export { DoOpposite, DoSameDirection };
