import React from 'react';
import './Plays.css';
import {
    GapUpDown, Sma20Trending, PriceBounceSma20, ValleyHill, StochasticsTrending, MacdCross,
    D15MTrending, YTDDaily, MacdSame, PriceCrossPrevious, PriceCloseNearBB, PriceCrossSma20, StochasticsCross} from './Indicators';
import { DoOpposite, DoSameDirection } from './Actions';

class CustomSign extends React.Component {
    render() {
        return (
            <view className="form-check">
                <input type="checkbox" className="form-check-input"></input>
                <label className="form-check-label">{this.props.text}</label>
            </view>
        );
    }
}

class Gap extends React.Component {
    render() {
        const gapH = this.props.spacing;
        return (
            <div style={{ height: gapH }}></div>  
        );
    }
}

class Play0 extends React.Component {
    render() {
        return (
            <view className="card"></view>
        );
    }
}

class Play1 extends React.Component {
    render() {
        return (
            <view className="card bg-light text-dark">
                <div className="card-header bg-dark text-light">1. REVERSE GAP SURPRISE</div>
                <div className="card-body">
                    <GapUpDown />
                    <CustomSign text="News today" />
                </div>
                <div className="card-footer"><DoOpposite opentime="9:45" closetime="10:30" pricetoopen="current price" pricetoclose="50% profit" /></div>
            </view>
        );
    }
}

class Play2 extends React.Component {
    render() {
        return (
            <view className="card bg-light text-dark">
                <div className="card-header bg-dark text-light">2. TRENDING WAVE 3 - SMA20 BOUNCE</div>
                <div className="card-body">
                    <D15MTrending />
                    <YTDDaily />
                    <ul>
                        <PriceBounceSma20 />
                        <Sma20Trending />
                        <ValleyHill />
                        <StochasticsTrending />
                        <MacdSame />
                        <PriceCrossPrevious />
                        <PriceCloseNearBB />
                    </ul>
                    <img className="card-img-bottom p-1" src="/charts/pricebouncesma20.jpg" alt="Card chart"></img>
                </div>
                <div className="card-footer"><DoSameDirection /></div>
            </view>
        );
    }
}

class Play3 extends React.Component {
    render() {
        return (
            <view className="card bg-light text-dark">
                <div className="card-header bg-dark text-light">3. REVERSAL WAVE 4 - MACD CROSSES NEW TREND</div>
                <div className="card-body">
                    <D15MTrending />
                    <YTDDaily />
                    <ul>
                        <PriceCrossSma20 />
                        <MacdCross />
                        <CustomSign text="Previous long trend and opposite of current crossed signals" />
                    </ul>
                    <img className="card-img-bottom p-1" src="/charts/play3_reversal_wave4.jpg" alt="Card chart"></img>
                </div>
                <div className="card-footer"><DoSameDirection /></div>
            </view>
        );
    }
}

class Play4 extends React.Component {
    render() {
        return (
            <view className="card bg-light text-dark">
                <div className="card-header bg-dark text-light">4. REVERSAL WAVE C - STOCH CROSSES TRENDING</div>
                <div className="card-body">
                    <D15MTrending />
                    <YTDDaily />
                    <ul>
                        <StochasticsCross />
                        <PriceBounceSma20 />
                        <PriceCrossPrevious />
                    </ul>
                    <img className="card-img-bottom p-1" src="/charts/play4_continuousreversal_wavec.jpg" alt="Card chart"></img>
                </div>
                <div className="card-footer"><DoSameDirection /></div>
            </view>
        );
    }
}

export { Gap, Play0, Play1, Play2, Play3, Play4 };
